.slideshow-panel {
  background-image: linear-gradient(#37eace, #00caba);
  min-height: 100px;
  display: flex;
  flex-direction: column; }
  .slideshow-panel .slides {
    margin: auto; }
  .slideshow-panel .icon {
    width: 32px;
    height: 32px;
    display: none; }
    .slideshow-panel .icon img {
      max-width: 100%;
      max-height: 100%;
      height: auto;
      width: auto; }
  @media (min-width: 768px) {
    .slideshow-panel {
      min-height: 100vh;
      flex-direction: column; }
      .slideshow-panel .icon {
        display: block; }
      .slideshow-panel .slides {
        display: flex;
        flex-direction: column;
        flex: 1; }
        .slideshow-panel .slides .text-wrapper {
          margin-top: auto; }
        .slideshow-panel .slides .slideshow-wrapper {
          margin-bottom: 30px; }
        .slideshow-panel .slides .dots-wrapper {
          margin-top: auto;
          margin-bottom: 30px; } }
