.slides {
  width: 100%;
  position: relative; }
  .slides .animate {
    animation: FadeIn .5s; }
  .slides .text-wrapper {
    text-align: center;
    margin-bottom: 25px;
    min-height: 5rem;
    font-size: 1.2rem;
    padding: 0 5%; }
  .slides .slideshow-wrapper {
    width: 100%;
    max-height: 100px;
    min-height: 100px; }
    .slides .slideshow-wrapper img {
      max-width: 100%;
      max-height: 100px;
      width: auto;
      height: auto; }
  .slides .dots-wrapper {
    margin-top: 30px;
    display: flex;
    justify-content: center;
    bottom: 0; }
    .slides .dots-wrapper .dot {
      display: inline-block;
      border-radius: 100%;
      width: 10px;
      height: 10px;
      background-color: #37eace;
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
      margin: 0 5px;
      cursor: pointer;
      transition: box-shadow 200ms; }
      .slides .dots-wrapper .dot.active {
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.5); }
  @media (min-width: 768px) {
    .slides .text-wrapper {
      font-size: 1.5rem;
      padding: 0 10%; }
    .slides .slideshow-wrapper {
      max-height: 250px;
      min-height: 250px; }
      .slides .slideshow-wrapper img {
        max-height: 250px; }
    .slides .dots-wrapper {
      margin-top: 50px; }
      .slides .dots-wrapper .dot {
        width: 13px;
        height: 13px; } }

@-webkit-keyframes FadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@-moz-keyframes FadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@-o-keyframes FadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes FadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }
