.button {
  display: inline-block;
  min-width: 90px;
  padding: 8px 10px;
  color: white;
  border-radius: 15px;
  cursor: pointer;
  user-select: none;
  text-align: center;
  font-size: .9rem; }

.rg {
  background-image: linear-gradient(#37eace, #00caba);
  min-width: 100px;
  box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.4); }

.tgl {
  position: relative;
  background-color: #44566c;
  z-index: 1;
  text-align: center; }
  .tgl::before {
    content: "";
    display: inline-block;
    width: 50%;
    height: 100%;
    position: absolute;
    z-index: -1;
    top: 0;
    box-shadow: 0 15px 10px rgba(0, 0, 0, 0.1);
    transition: left 200ms, border-radius 200ms;
    background-image: linear-gradient(#37eace, #00caba); }
  .tgl .text {
    z-index: 3;
    min-width: 90px;
    display: inline-block;
    width: 50%;
    transition: color 250ms; }
  .tgl.off .f-state {
    color: #808c9c; }
  .tgl.off::before {
    left: 50%;
    border-radius: 0px 15px 15px 0px; }
  .tgl.on .s-state {
    color: #808c9c; }
  .tgl.on::before {
    left: 0%;
    border-radius: 15px 0px 0px 15px; }
