.signin-panel {
  background-image: linear-gradient(#364a64, #212d40);
  box-shadow: -10px 2px 15px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column; }
  .signin-panel .button.tgl {
    float: right; }
  .signin-panel .form-wrapper {
    margin: auto 0;
    width: 100%;
    padding: 40px 5%; }
    .signin-panel .form-wrapper .title a {
      font-size: 1.2rem; }
    .signin-panel .form-wrapper .title .shadow-text {
      font-size: .9rem; }
    .signin-panel .form-wrapper form {
      margin-top: 30px; }
      .signin-panel .form-wrapper form .input {
        margin: 15px 0; }
      .signin-panel .form-wrapper form .checkbox {
        display: flex;
        margin-bottom: 30px; }
        .signin-panel .form-wrapper form .checkbox .checkbox-text {
          display: inline-block;
          height: 100%;
          align-items: center; }
      .signin-panel .form-wrapper form .button {
        display: inline-block;
        margin-left: 3px; }
      .signin-panel .form-wrapper form .extra-link {
        display: inline-block;
        margin-left: 15px; }
        .signin-panel .form-wrapper form .extra-link a {
          color: #808c9c; }
  @media (min-width: 768px) {
    .signin-panel {
      min-height: 100vh; }
      .signin-panel .form-wrapper {
        padding: 10%; } }
