.text-input {
  text-align: left;
  font-size: .9rem;
  position: relative; }
  .text-input::after {
    content: "";
    display: block;
    background-color: white;
    width: 0;
    position: absolute;
    height: 1px;
    bottom: 0px;
    margin: 0 50%;
    transition: width 200ms, margin 200ms; }
  .text-input input {
    box-sizing: border-box;
    width: 100%;
    margin-top: 7px;
    padding: 0px 3px 5px;
    min-height: 30px;
    font-size: .9rem;
    display: block;
    outline: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background-color: rgba(0, 0, 0, 0);
    border: 0px;
    border-bottom: 1px solid #808c9c;
    color: white; }
    .text-input input::placeholder {
      color: #808c9c; }
  .text-input.focus::after {
    width: 100%;
    margin: 0%; }
  .text-input.error input::placeholder {
    color: #fb5563; }
  .text-input.error::after {
    width: 100%;
    margin: 0%;
    background-color: #fb5563; }

.checkbox .check {
  width: 18px;
  height: 18px;
  background-color: #44566c;
  display: inline-block;
  border-radius: 3px;
  cursor: pointer;
  padding: 2px;
  position: relative;
  transition: background-color 200ms;
  user-select: none;
  margin-right: 5px; }
  .checkbox .check::after {
    content: "";
    display: block;
    height: 16px;
    width: 8px;
    border-bottom: 4px solid #212d40;
    border-right: 4px solid #212d40;
    margin: -2px 0 0 3px;
    transform: rotate(45deg);
    opacity: 0;
    transition: opacity 150ms; }
  .checkbox .check.checked {
    background-color: #00caba; }
    .checkbox .check.checked::after {
      opacity: 1; }
